import React, { useEffect, useState } from "react";
import axios from "axios";
import { Button, Form, Modal } from "semantic-ui-react";
import { storage } from "../hooks/firebase";
import { uploadBytes, getDownloadURL, ref } from "firebase/storage";
import styles from "../style/Input.module.css";

function BookUpdateModal({ open, setOpen, data }) {
  useEffect(() => {
    setBookID(data.bookID);
    setTitle(data.title);
    setAuthor(data.author);
    setPublisher(data.publisher);
    setYear(data.year);
    setGenre(data.genre);
    setAddress(data.address);
    setBookImg(data.bookImg);
    setPage(data.page);
  }, [data]);

  const [bookID, setBookID] = useState("");
  const [title, setTitle] = useState("");
  const [author, setAuthor] = useState("");
  const [publisher, setPublisher] = useState("");
  const [year, setYear] = useState("");
  const [genre, setGenre] = useState("");
  const [address, setAddress] = useState("");
  const [bookImg, setBookImg] = useState("");
  const [page, setPage] = useState("");
  const [loading, setLoading] = useState(false);
  const [err, setErr] = useState("");

  async function UpdateBook() {
    setErr("");
    setLoading(true);
    await axios({
      url: `${process.env.REACT_APP_HOST}/admin/book/update`,
      method: "POST",
      withCredentials: true,
      data: {
        bookID: bookID,
        title: title,
        author: author,
        publisher: publisher,
        year: year,
        genre: genre,
        address: address,
        bookImg: bookImg,
        page: page,
      },
    })
      .then((result) => {
        if (result.status === 200) {
          console.log("Save Book Complete!");
          window.location.reload();
        }
      })
      .catch((err) => {
        if (err.response.data === "Value Not Found") {
          setErr("  .");
          setLoading(false);
        } else {
          setErr(" .     .");
          setLoading(false);
        }
      });
  }

  async function ImageHandler(e) {
    setLoading(true);
    const file = e.target.files[0];

    try {
      const storageRef = ref(storage, `book/${genre}/${Date.now()}`);
      await uploadBytes(storageRef, file).then((snapshot) => {
        getDownloadURL(snapshot.ref).then((url) => {
          setBookImg(url);
          setLoading(false);
        });
      });
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  }

  const yearOptions = [];

  for (let year = 1990; year <= 2023; year++) {
    yearOptions.push({
      key: String(year),
      value: String(year),
      text: String(year),
    });
  }

  const genreOptions = [
    { key: "IT", value: "IT", text: "IT" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
  ];

  async function DeleteBook(id) {
    setErr("");
    setLoading(true);
    await axios({
      url: `${process.env.REACT_APP_HOST}/admin/book/delete`,
      method: "POST",
      withCredentials: true,
      data: {
        bookID: id,
      },
    })
      .then((result) => {
        if (result.status === 200) {
          console.log("Delete Book Complete!");
          window.location.reload();
        }
      })
      .catch((err) => {
        console.log(err);
        setLoading(false);
      });
  }

  function DeleteBookHandler() {
    if (window.confirm(`(${bookID}) ${title}\n ?`)) {
      DeleteBook(bookID);
    }
  }

  return (
    <Modal
      onClose={() => setOpen(false)}
      onOpen={() => setOpen(true)}
      open={open}
    >
      <Modal.Header>  </Modal.Header>
      <Modal.Content>
        <Form>
          <Form.Group widths={2}>
            <Form.Input
              label=" ( )"
              placeholder="BOOK ID"
              value={bookID || ""}
              readOnly
            />
            <Form.Input
              label=" "
              placeholder="ADDRESS"
              onChange={(e) => setAddress(e.target.value)}
              value={address || ""}
            />
          </Form.Group>
          <Form.Group widths={2}>
            <Form.Input
              label=" ( )"
              placeholder="TITLE"
              onChange={(e) => setTitle(e.target.value)}
              value={title || ""}
              readOnly
            />
            <Form.Input
              label=""
              placeholder="AUTHOR"
              onChange={(e) => setAuthor(e.target.value)}
              value={author || ""}
            />
          </Form.Group>
          <Form.Group widths={2}>
            <Form.Input
              label=""
              placeholder="PUBLISHER"
              onChange={(e) => setPublisher(e.target.value)}
              value={publisher || ""}
            />
            <Form.Input
              label=" "
              placeholder="PAGE"
              onChange={(e) => setPage(e.target.value)}
              value={page || ""}
            />
          </Form.Group>
          <Form.Group widths={2}>
            <Form.Select
              label=" "
              placeholder="YEAR"
              onChange={(e, result) => setYear(result.value)}
              value={year || ""}
              options={yearOptions}
            />
            <Form.Select
              label=""
              placeholder="GENRE"
              onChange={(e, result) => setGenre(result.value)}
              value={genre || ""}
              options={genreOptions}
            />
          </Form.Group>
          <Form.Group>
            <Form.Input label="">
              <Button>
                <label htmlFor="file" className={styles.labelBookImg}>
                  {bookImg}
                </label>
              </Button>
            </Form.Input>
            <input
              id="file"
              style={{ display: "none" }}
              type="file"
              accept="image/*"
              placeholder="IMAGE"
              onChange={(e) => ImageHandler(e)}
            />
          </Form.Group>
          <div className={styles.errDiv}>
            {err && <p className={styles.errText}>{err}</p>}
          </div>
        </Form>
      </Modal.Content>
      {!loading ? (
        <Modal.Actions>
          <Button onClick={DeleteBookHandler} negative floated="left">
            
          </Button>
          <Button onClick={() => setOpen(false)}></Button>
          <Button onClick={UpdateBook} positive>
            
          </Button>
        </Modal.Actions>
      ) : (
        <Modal.Actions>
          <Button negative floated="left" loading>
            
          </Button>
          <Button loading></Button>
          <Button positive loading>
            
          </Button>
        </Modal.Actions>
      )}
    </Modal>
  );
}

export default BookUpdateModal;
