import { useState } from "react";
import axios from "axios";
import { storage } from "../hooks/firebase";
import { uploadBytes, getDownloadURL, ref } from "firebase/storage";
import {
  Form,
  Button,
  Segment,
  Modal,
  Icon,
  Image,
  Container,
} from "semantic-ui-react";
import styles from "../style/Input.module.css";
import imgDefault from "../assets/book/imgDefault.png";
import BookDeatail from "./BookDetail";

function BookForm() {
  const [title, setTitle] = useState("");
  const [author, setAuthor] = useState("");
  const [publisher, setPublisher] = useState("");
  const [year, setYear] = useState("");
  const [genre, setGenre] = useState("");
  const [address, setAddress] = useState("");
  const [bookImg, setBookImg] = useState("imgDefault");
  const [page, setPage] = useState("");
  const [loading, setLoading] = useState(false);
  const [err, setErr] = useState("");
  const [open, setOpen] = useState(false);
  const [preview, setPreview] = useState({});
  const [previewOpen, setPreviewOpen] = useState(false);

  async function SaveBook() {
    setErr("");
    setLoading(true);
    await axios({
      url: `${process.env.REACT_APP_HOST}/admin/book/insert`,
      method: "POST",
      withCredentials: true,
      data: {
        title: title,
        author: author,
        publisher: publisher,
        year: year,
        genre: genre,
        address: address,
        bookImg: bookImg,
        page: page,
      },
    })
      .then((result) => {
        if (result.status === 200) {
          console.log("Save Book Complete!");
          try {
            axios({
              url: `${process.env.REACT_APP_HOST}/admin/bookstat/validate`,
              params: { title: title },
              method: "GET",
              withCredentials: true,
            })
              .then((res) => {
                if (res.data.length > 0) {
                  setOpen(true);
                  setLoading(false);
                } else {
                  SaveBookStat();
                  setOpen(true);
                  setLoading(false);
                }
              })
              .catch((err) => {
                console.log(err);
              });
          } catch (err) {
            console.log(err);
          }
        }
      })
      .catch((err) => {
        if (err.response.data === "Value Not Found") {
          setErr("  .");
          setLoading(false);
        } else {
          setErr(" .     .");
          setLoading(false);
        }
      });
  }

  async function SaveBookStat() {
    await axios({
      url: `${process.env.REACT_APP_HOST}/admin/bookstat/insert`,
      method: "POST",
      withCredentials: true,
      data: {
        title: title,
      },
    })
      .then((result) => {
        if (result.status === 200) {
          console.log("Save BookStat Complete!");
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }

  async function ImageHandler(e) {
    setLoading(true);
    const file = e.target.files[0];

    try {
      const storageRef = ref(storage, `book/${genre}/${Date.now()}`);
      await uploadBytes(storageRef, file).then((snapshot) => {
        getDownloadURL(snapshot.ref).then((url) => {
          setBookImg(url);
          setLoading(false);
        });
      });
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  }

  function PreviewHandler() {
    setPreview({
      title: title,
      author: author,
      publisher: publisher,
      year: year,
      genre: genre,
      address: address,
      page: page,
    });
    setPreviewOpen(true);
  }

  const yearOptions = [];

  for (let year = 1990; year <= 2023; year++) {
    yearOptions.push({
      key: String(year),
      value: String(year),
      text: String(year),
    });
  }

  const genreOptions = [
    { key: "IT", value: "IT", text: "IT" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
    { key: "", value: "", text: "" },
  ];

  return (
    <Container>
      <Segment>
        <Form>
        <Form.Group widths={2}>
            <Form.Input
              label=""
              placeholder="BOOK ID"
              value={"BOOK ID"}
              readOnly
            />
            <Form.Input
              label=" "
              placeholder="ADDRESS"
              onChange={(e) => setAddress(e.target.value)}
              value={address}
            />
          </Form.Group>
          <Form.Group widths={2}>
            <Form.Input
              label=" (   )"
              placeholder="TITLE"
              onChange={(e) => setTitle(e.target.value)}
              value={title}
            />
            <Form.Input
              label=""
              placeholder="AUTHOR"
              onChange={(e) => setAuthor(e.target.value)}
              value={author}
            />
          </Form.Group>
          <Form.Group widths={2}>
            <Form.Input
              label=""
              placeholder="PUBLISHER"
              onChange={(e) => setPublisher(e.target.value)}
              value={publisher}
            />
            <Form.Input
              label=" "
              placeholder="PAGE"
              onChange={(e) => setPage(e.target.value)}
              value={page}
            />
          </Form.Group>
          <Form.Group widths={2}>
            <Form.Select
              label=" "
              placeholder="YEAR"
              onChange={(e, result) => setYear(result.value)}
              value={year}
              options={yearOptions}
            />
            <Form.Select
              label=""
              placeholder="GENRE"
              onChange={(e, result) => setGenre(result.value)}
              value={genre}
              options={genreOptions}
            />
          </Form.Group>
          <Form.Group>
            <Form.Input label="">
              <Button>
                <label htmlFor="file" className={styles.labelBookImg}>
                  {bookImg}
                </label>
              </Button>
            </Form.Input>
            <input
              id="file"
              style={{ display: "none" }}
              type="file"
              accept="image/*"
              placeholder="IMAGE"
              onChange={(e) => ImageHandler(e)}
            />
          </Form.Group>
          <div className={styles.errDiv}>
            {err && <p className={styles.errText}>{err}</p>}
          </div>
          <br />
          <br />
          {!loading ? (
            <Button
              style={{ marginTop: "-40px" }}
              floated="right"
              size="large"
              onClick={SaveBook}
              positive
            >
              
            </Button>
          ) : (
            <Button
              style={{ marginTop: "-40px" }}
              floated="right"
              size="large"
              positive
              loading
            >
              
            </Button>
          )}
          {!loading ? (
            <Button
              style={{ marginTop: "-40px" }}
              floated="right"
              size="large"
              onClick={PreviewHandler}
              primary
            >
              
            </Button>
          ) : (
            <Button
              style={{ marginTop: "-40px" }}
              floated="right"
              size="large"
              onClick={PreviewHandler}
              primary
              loading
            >
              
            </Button>
          )}
        </Form>
        <Modal
          onClose={() => setOpen(false)}
          onOpen={() => setOpen(true)}
          open={open}
          closeOnDimmerClick={false}
        >
          <Modal.Header>  </Modal.Header>
          <Modal.Content>
            <Modal.Description>
              <p> .</p>
            </Modal.Description>
          </Modal.Content>
          <Modal.Actions>
            <Button onClick={() => window.open("/admin/book", "_self")} positive>
               
              <Icon name="right chevron" />
            </Button>
          </Modal.Actions>
        </Modal>
        <Modal
          onClose={() => setPreviewOpen(false)}
          onOpen={() => setPreviewOpen(true)}
          open={previewOpen}
        >
          <Modal.Header></Modal.Header>
          <Modal.Content>
            {bookImg === "imgDefault" ? (
              <Image
                style={{ width: "132px", height: "200px" }}
                src={imgDefault}
              />
            ) : (
              <Image
                style={{ width: "132px", height: "200px" }}
                src={bookImg}
              />
            )}
            <BookDeatail data={preview} />
          </Modal.Content>
          <Modal.Content> : {address}</Modal.Content>
          <Modal.Actions>
            <Button onClick={() => setPreviewOpen(false)}>
              
            </Button>
          </Modal.Actions>
        </Modal>
      </Segment>
    </Container>
  );
}

export default BookForm;
